import React from 'react';

import './StudentEntryUI.css';
import Processing from '../../assets/images/processing.gif';
import Loading from '../Loading/Loading';

const StudentEntryUI = ( props ) => {

    return (
        <div className="StudentEntryUI">
            <div className="StudentEntryUI-Content">

                <div className="EntryForm">
                    <div>
                        <h5 style={ { fontWeight: 'bold' } }>Student Information</h5>
                        <hr />
                        <hr />
                        <hr />
                        {
                            props.ShowLoading
                            ?
                            <Loading loadingHeight="48vh" image={ Processing } txt='Processing' width="50" height="50" unit="%" />
                            :
                            null
                        }
                        <form onSubmit={ props.onStepOne } className="StepForms">
                            <input 
                                type="text" 
                                name="studentName" 
                                value={ props.StudentInfo.studentName }
                                onChange={ props.onChangeHandler } 
                                className="inputs" 
                                placeholder="Student Name" 
                                pattern="[A-Za-z]+"
                                minLength="3"
                                required
                             />
                            <input 
                                type="text" 
                                name="Surname" 
                                value={ props.StudentInfo.Surname }
                                onChange={ props.onChangeHandler } 
                                className="inputs" 
                                placeholder="Surname" 
                                pattern="[A-Za-z]+"
                                minLength="2"
                                required
                             />
                            <select 
                                type="text" 
                                name="Gender" 
                                value={ props.StudentInfo.Gender }
                                onChange={ props.onChangeHandler } 
                                className="inputs" 
                                placeholder="Gender" 
                                required
                            >
                                <option value="">Gender</option>
                                <option value="Male">Male</option>
                                <option value="FeMale">Female</option>
                            </select>
                            <input 
                                type="text" 
                                name="MotherTongue" 
                                value={ props.StudentInfo.MotherTongue }
                                onChange={ props.onChangeHandler } 
                                className="inputs" 
                                placeholder="Mother Tongue" 
                                pattern="[A-Za-z]+"
                                minLength="2"
                                required
                             />
                            <button type="submit" className="btns">Next</button>
                            <p className="PrevStep" onClick={ props.BackToList }> <small>Back To List</small> </p>
                        </form>

                        <form onSubmit={ props.onStepTwo } className="StepForms">
                            <input 
                                type="text" 
                                name="FatherName" 
                                value={ props.StudentInfo.FatherName }
                                onChange={ props.onChangeHandler } 
                                className="inputs" 
                                placeholder="Father Name" 
                                pattern="[A-Za-z]+"
                                minLength="3"
                                required
                             />
                            <input 
                                type="text" 
                                name="CNIC" 
                                value={ props.StudentInfo.CNIC }
                                onChange={ props.onChangeHandler } 
                                className="inputs" 
                                placeholder="Father CNIC" 
                                minLength="13"
                                maxLength="13"
                                pattern="[0-9]+"
                                required
                             />
                            <input 
                                type="text" 
                                name="Occupation" 
                                value={ props.StudentInfo.Occupation }
                                onChange={ props.onChangeHandler } 
                                className="inputs" 
                                placeholder="Father's / Guardian's Occupation" 
                                pattern="[A-Za-z]+"
                                minLength="3"
                                required
                             />
                            <input 
                                type="number" 
                                name="MonthlyIncome" 
                                value={ props.StudentInfo.MonthlyIncome }
                                onChange={ props.onChangeHandler } 
                                className="inputs" 
                                placeholder="Monthly Income" 
                                pattern="[0-9]+"
                                min="1"
                                required
                             />
                            <button type="submit" className="btns">Next</button>
                            <p className="PrevStep" onClick={ () => props.PreviousStep('one') }> <small>Previous Step</small> </p>
                        </form>

                        <form onSubmit={ props.onStepThird } className="StepForms">
                            <input 
                                type="date" 
                                name="DateOfBirth" 
                                value={ props.StudentInfo.DateOfBirth }
                                onChange={ props.onChangeHandler } 
                                className="inputs" 
                                placeholder="Date Of Birth" 
                                required
                             />
                            <input 
                                type="text" 
                                name="PlaceOfBirth" 
                                value={ props.StudentInfo.PlaceOfBirth }
                                onChange={ props.onChangeHandler } 
                                className="inputs" 
                                placeholder="Place Of Birth" 
                                minLength="3"
                                required
                             />
                            <input 
                                type="text" 
                                name="Province" 
                                value={ props.StudentInfo.Province }
                                onChange={ props.onChangeHandler } 
                                className="inputs" 
                                placeholder="Province" 
                                pattern="[A-Za-z]+"
                                minLength="3"
                                required
                             />
                            <input 
                                type="text" 
                                name="ResidentialAddress" 
                                value={ props.StudentInfo.ResidentialAddress }
                                onChange={ props.onChangeHandler } 
                                className="inputs" 
                                placeholder="Residential Address" 
                                minLength="10"
                                required
                             />
                            <button type="submit" className="btns">Next</button>
                            <p className="PrevStep" onClick={ () => props.PreviousStep('two') }> <small>Previous Step</small> </p>
                        </form>

                        <form onSubmit={ props.onStepForth } className="StepForms">
                            <input 
                                type="text" 
                                name="Religion" 
                                value={ props.StudentInfo.Religion }
                                onChange={ props.onChangeHandler } 
                                className="inputs" 
                                placeholder="Religion" 
                                minLength="5"
                                pattern="[A-Za-z]+"
                                required
                             />
                            <input 
                                type="text" 
                                name="PreviousSchool" 
                                value={ props.StudentInfo.PreviousSchool }
                                onChange={ props.onChangeHandler } 
                                className="inputs" 
                                placeholder="Previous School" 
                                minLength="5"
                                required
                             />
                            <input 
                                type="text" 
                                name="OfficeTelephone" 
                                value={ props.StudentInfo.OfficeTelephone }
                                onChange={ props.onChangeHandler } 
                                className="inputs" 
                                placeholder="Office telephone" 
                                minLength="11"
                                pattern="[0-9]+"
                                required
                             />
                            <input 
                                type="text" 
                                name="ResidenceTelephone" 
                                value={ props.StudentInfo.ResidenceTelephone }
                                onChange={ props.onChangeHandler } 
                                className="inputs" 
                                placeholder="Residential Telephone" 
                                minLength="11"
                                pattern="[0-9]+"
                                required
                             />
                            <button type="submit" className="btns">Submit</button>
                            <p className="PrevStep" onClick={ () => props.PreviousStep('three') }> <small>Previous Step</small> </p>
                        </form>

                        <div className="StepForms">
                            <p id="StudentInfoMsg"></p>
                            <div className="container-fluid d-none">
                                <div className="row">
                                    <div className="col-6">
                                        <button onClick={ props.BackToList } type="button" style={ { fontSize: '13px' } } className="btns">Back To List</button>
                                    </div>
                                    <div className="col-6">
                                        <button onClick={ props.NewEntry } type="button" style={ { fontSize: '13px' } } className="btns">New Entry</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="StudentEntryUI-inner">
                    <div>

                        <div className="Steps">
                            <div>
                                <div className="Step">
                                    <div className="Step-inner">
                                        {
                                            props.Step1
                                            ?
                                            <i className="las la-check"></i>
                                            :
                                            <span>1</span>
                                        }
                                    </div>
                                </div>
                                <small> { props.Step1 ? 'Complete' : null } </small>
                            </div>
                            <div className="line"></div>
                            <div>
                                <div className="Step">
                                    <div className="Step-inner">
                                        {
                                            props.Step2
                                            ?
                                            <i className="las la-check"></i>
                                            :
                                            <span>2</span>
                                        }
                                    </div>
                                </div>
                                <small> { props.Step2 ? 'Complete' : null } </small>
                            </div>
                                <div className="line"></div>
                            <div>
                                <div className="Step">
                                    <div className="Step-inner">
                                        {
                                            props.Step3
                                            ?
                                            <i className="las la-check"></i>
                                            :
                                            <span>3</span>
                                        }
                                    </div>
                                </div>
                                <small> { props.Step3 ? 'Complete' : null } </small>
                            </div>
                                <div className="line"></div>
                            <div>
                                <div className="Step">
                                    <div className="Step-inner">
                                        {
                                            props.Step4
                                            ?
                                            <i className="las la-check"></i>
                                            :
                                            <span>4</span>
                                        }
                                    </div>
                                </div>
                                    <small> { props.Step4 ? 'Complete' : null } </small>
                            </div>
                            <div className="line"></div>
                        </div>
                        
                    </div>

                </div>

            </div>
        </div>
    )

}

export default StudentEntryUI;